import React from "react"
import {Link} from "react-router-dom"

import Stars from "../stars/Stars"
const Product = ({id,img,title,originPrice,price,stars}) => {
    
   
    return (
        <div className="product mb-4">
            <Link className="img" to={`/products/single_product/${id}`}>
                <img src={img} alt="product-img" />
                <div className="overlay">
                    <Link className="btn" to={`/products/single_product/${id}`}>
                        Add to Cart +
                    </Link>
                </div>
                {originPrice ? <span className="percent">
                    { 100 - Math.round(parseInt(price.replace("$","")) / parseInt(originPrice.replace("$","")) * 100) }%
                </span> : ""}
            </Link>
           <div className="white-part pb-4 pt-3 px-3">
                <Link className="title" to={`/products/${id}`}>{title}</Link>
                <div className="info">
                    <div className="price">
                        <span>{originPrice ? originPrice : ""} </span>
                        { price}
                    </div>
                    <Stars stars={stars} />
                </div>
           </div>
        </div>
    )
}



export default Product