import React from "react"

import {Container,Row,Col} from "react-bootstrap"
import data from "../../../jsons/products"
import Product from "../product/Product"
const ShopProducts = () => {
    return (
        <div className="shop-products">
            <Container>
                <Row>
                    {data().map(item => {
                    const {id,img,originPrice,price,stars} = item
                    return (
                        <Col key={id} xs="12" md="6" lg="4" xl="3" className="mb-4" >
                            <Product id={id} img={img} originPrice={originPrice} price={price} stars={stars} />
                        </Col>
    )
        })}
                </Row>
            </Container>
        </div>
    )
}



export default ShopProducts